import row from '@/components/conmon/row.vue';
export default {
	components: {
		row
	},
	data() {
		return {
			Balance: 256
		};
	},
	computed: {
		myBalance() {
			return this.Balance.toFixed(2);
		}
	},
	methods: {
		toRecharge() {
			uni.navigateTo({
				url: './recharge'
			});
		},
		toChange() {
			uni.navigateTo({
				url: './change'
			});
		}
	}
};
